.cart__popover__wrapper {
    cursor: pointer;
    transition: color 300ms;
}

.cart__popover__wrapper:hover {
    color: #ff9f1c;
}

.cart__popover__wrapper--active {
    cursor: pointer;
    transition: color 300ms;
}

.cart__popover__wrapper--active:hover{
    color: #fdfffc;
  }
  

.cart__view__list__item {
    display: flex;
    width: 100%;
    padding: 10px 0;
    border-bottom: 1px solid rgb(197, 197, 197);
}

.cart__view__list__item img {
    width: 70px; 
    height: 60px;
    object-fit: cover;
    margin-right: 10px;
}
.cart__view__list__item__title {
    font-weight: bold;
}

.cart__view__list__item__author {
    font-size: 13px;
}

.checkout__wrapper {
    font-weight: bold;
}

.checkout__total__price, .checkout__total__price--no__button {
    display: flex;
    justify-content: space-between;
