    cursor: pointer;
    transition: all 0.3s;
}

.checkout__link button {
    border: 1px solid #18ac9d;
    background-color: #edf2f4;
    color: #18ac9d;
}

.checkout__link button:hover {
    background-color: #18ac9d;
    color: #edf2f4;
}

.cart__view__list--empty {
    padding: 15px;
    text-align: center;
}

.homepage__link button {
    border: 1px solid #E71D36;
    background-color: #edf2f4;
    color: #E71D36;
}

.homepage__link button:hover {
    background-color: #E71D36;
    color: #edf2f4;
}
.header__nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
  background-color: #fdfffc;
  transition: all 300ms;
}

.header__nav div{
  display: flex;
  padding: 0 5px;
}

.header__nav a,
.nav__btn--logout{
  border: none;
  padding: 18px 6px;
  background-color: transparent;
  color: #000;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 500;
  font-size: 0.875rem;
  letter-spacing: 0.01rem;
  text-decoration: none;
  cursor: pointer;
  transition: color 300ms;
}

.header__nav a:hover,
.nav__btn--logout:hover{
  color: #ff9f1c;
}

.header__nav--active{
  background-color: #ff9f1c;
  background-image: linear-gradient(to right, #ff9f1c, rgba(1, 22, 39, 0.1));
}

.header__nav--active a:hover,
.header__nav--active .nav__btn--logout:hover{
  color: #fdfffc;
}

.header__nav__logo__container a{
  padding: 10px 5px 6px;
}

.header__nav__logo__container img{
  width: 80px;
}

.header__nav__link__container{
  align-items: center;
}

@media (min-width: 281px){
  .header__nav a,
  .nav__btn--logout{
    padding: 18px 10px;
    font-size: 1rem;
    font-weight: 400;
  }
}

@media (min-width: 380px){
  .header__nav div{
    padding: 0 15px;
  }

  .header__nav__logo__container a{
    padding: 14px 10px 10px;
  }

  .header__nav__logo__container img{
    width: 100px;
  }
}

@media (min-width: 600px){
  .header__nav{
    top: 40px;
  }

  .header__nav--active{
    top: 0;
  }
}
