.course__details{
  display: flex;
  flex-direction: column;
  max-width: 1215px;
  margin: 50px auto;
  padding: 0 30px;
}

.course__details__top__wrapper{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-gap: 25px;
  gap: 25px;
  margin-bottom: 40px;
}

.course__details__info__wrapper{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.course__details__name{
  margin-bottom: 30px;
  font-size: 1.375rem;
  font-weight: 500;
}

.course__details__info,
.course__details__info--left,
.course__details__info--right{
  display: flex;
  flex-direction: column;
}

.course__details__info{
  grid-gap: 25px;
  gap: 25px;
}

.course__details__info--left{
  grid-gap: 8px;
  gap: 8px;
}

.course__details__info--right{
  grid-gap: 12px;
  gap: 12px;
}

.course__details__info__hours__and__level{
  display: flex;
  grid-gap: 15px;
  gap: 15px;
}

.course__details__info__category,
.course__details__info__author,
.course__details__info__hours__and__level{
  font-size: 0.875rem;
}

.course__details__info__rating{
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  font-size: 1rem;
  font-weight: 500;
  color: #ff9f1c;
}

.course__details__info__price{
  font-size: 1.125rem;
  font-weight: 500;
}

.course__details__info__btn{
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #18ac9d;
  background-color: #edf2f4;
  color: #18ac9d;
  font-size: 0.938rem;
  font-weight: 500;
  letter-spacing: 0.025rem;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 300ms;
}

.course__details__info__btn:hover{
  background-color: #18ac9d;
  color: #edf2f4;
}

.course__details__image{
  display: flex;
}

.course__details__image img{
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.course__details h4{
  display: inline-flex;
  position: relative;
  margin-bottom: 20px;
  font-size: 1.375rem;
  font-weight: 500;
}

.course__details h4:before{
  content: "";
  position: absolute;
  top: calc(1.75ex);
  left: calc(0.5ch);
  z-index: -1;
  height: 30%;
  width: 100%;
  background-color: rgb(255, 159, 28, 0.85);
}

.course__details__description,
.course__details__requirements,
.course__details__content,
.course__details__goals{
  margin-bottom: 30px;
}

.course__details__description p,
.course__details__requirements p{
  margin-bottom: 15px;
  font-size: 0.938rem;
  line-height: 1.375rem;
}

.course__details__description p:last-child,
.course__details__requirements p:last-child{
  margin-bottom: 0;
}

.course__details__content ol,
.course__details__goals ul{
  list-style: none;
}

.course__details__content li,
.course__details__goals li{
  margin-bottom: 10px;
  font-size: 0.938rem;
  line-height: 1.375rem;
}

.course__details__content li:last-child,
.course__details__goals li:last-child{
  margin-bottom: 0;
}

.course__details__goals li{
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
  font-size: 0.938rem;
  line-height: 1.375rem;
}

.course__details__content ol li:before{
  content: counter(list-item) ".";
  padding-right: 5px;
  font-size: 1.375rem;
  font-weight: 500;
}

.course__details__reviews h4{
  margin-bottom: 30px;
}

.course__details__review__wrapper{
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.course__details__review__wrapper:last-child{
  margin-bottom: 0;
}

.course__details__review__image{
  display: flex;
}

.course__details__review__wrapper img{
  width: 50px;
  height: 50px;
  object-fit: cover;
  margin: 0 15px 15px 0;
  border-radius: 50%;
}

.course__details__review__content h5{
  font-size: 0.938rem;
  font-weight: 500;
  margin-bottom: 10px;
}

.course__details__review__content p{
  font-size: 0.938rem;
  font-style: italic;
  line-height: 1.375rem;
}

@media (min-width: 375px){
  .course__details__name{
    font-size: 1.5rem;
  }

  .course__details h4{
    font-size: 1.5rem;
  }
  
  .course__details__description p,
  .course__details__requirements p{
    font-size: 1rem;
    line-height: 1.5rem;
  }

  .course__details__content li,
  .course__details__goals li{
    margin-bottom: 15px;
    font-size: 1rem;
    line-height: 1.5rem;
  }

  .course__details__review__wrapper{
    flex-direction: row;
  }

  .course__details__review__wrapper img{
    width: 60px;
    height: 60px;
  }

  .course__details__review__content h5{
    font-size: 1rem;
  }
  
  .course__details__review__content p{
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

@media (min-width: 600px){
  .course__details__name{
    font-size: 1.75rem;
  }

  .course__details__info__rating{
    margin: 10px 0;
    font-size: 1rem;
  }

  .course__details__info__category,
  .course__details__info__author,
  .course__details__info__hours__and__level{
    font-size: 0.938rem;
  }
}

@media (min-width: 700px){
  .course__details__top__wrapper{
    flex-direction: row;
  }

  .course__details__info__wrapper{
    width: 50%;
  }

  .course__details__image{
    width: 50%;
  }
}

@media (min-width: 865px){
  .course__details__info{
    flex-direction: row;
  }

  .course__details__info--left{
    width: 50%;
  }
  
  .course__details__info--right{
    justify-content: flex-end;
    width: 50%;
  }

  .course__details__info__price{
    font-size: 1.375rem;
  }
}

@media (min-width: 1024px){
  .course__details__top__wrapper{
    max-height: 300px;
  }

  .course__details__info__wrapper{
    width: 60%;
  }

  .course__details__name{
    font-size: 2rem;
  }

  .course__details__info__btn{
    width: 85%;
  }

  .course__details__image{
    width: 40%;
  }
}

/*STYLING OF AN EMPTY BASKET ELEMENTS*/
.cart__wrapper h2 {
    margin: 25px auto;
    max-width: 60%;
}

.empty__cart__message {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 25px auto;
    padding: 25px;
    border: 1px solid rgb(194, 194, 194);
    max-width: 75%;
}

.empty__cart__message p {
    text-align: center;
}

.empty__cart__message button {
    margin-top: 10px;
    padding: 12px 16px;
    border: 1px solid #E71D36;
    color: #E71D36;
    font-size: 0.875rem;
    font-weight: 500;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s;
}

.empty__cart__message button:hover {
    background-color: #E71D36;
    color: #edf2f4;
}

/*STYLING OF THE ELEMENTS IN THE CART LIST*/
.shopping__cart__container {
    display: flex;
    justify-content: center;
    grid-gap: 25px;
    gap: 25px;
    margin: 25px auto;
    max-width: 75%;
}

.shopping__cart__list__item {
    display: flex;
    justify-content: space-between;
    grid-gap: 30px;
    gap: 30px;
    padding: 10px;
    border: 1px solid rgb(197, 197, 197);
}

.shopping__cart__list__item img {
    max-width: 130px;
    object-fit: cover;
}

.shopping__cart__list__item button {
    border: none;
    cursor: pointer;
}

.cart__summary__info, .cart__price__checkout {
    display: flex;
    align-items: center;
}

.cart__price__checkout p {
    text-align: right;
}

.cart__summary__info {
    grid-gap: 20px;
    gap: 20px;
}

.cart__price__checkout {
    grid-gap: 30px;
    gap: 30px;
}

.cart__price__checkout button {
    padding: 10px 8px 8px;
    background-color: rgb(216, 216, 216);
    transition: background-color 0.3s, color 0.3s;
}

.cart__price__checkout button:hover {
    background-color: #E71D36;
    color: #edf2f4;
}

.trash-icon {
    transition: transform 0.3s;  
}

.trash-icon:hover {
    transform: rotate(360deg);
}

.cart__course__title p:first-child {
    font-weight: bold;
}

.cart__course__title p:nth-child(2) {
    font-size: 14px;
}

.cart__course__total button {
    min-width: 250px;
    margin-top: 15px;
    padding: 12px 16px;
    border: 1px solid #18ac9d;
    background-color: #edf2f4;
    color: #18ac9d;
    font-size: 0.875rem;
    font-weight: 500;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s;
}

.cart__course__total button:hover {
    background-color: #18ac9d;
    color: #edf2f4;
}

.cart__course__total__price {
    margin-top: 5px;
    font-size: 25px;
    font-weight: bold;
}

.course__details__link {
    text-decoration: none;
    color: #000;
    color: initial;
}

@media screen and (max-width: 1284px) {
    .cart__wrapper h2 {
        max-width: 75%;
    }
}

@media screen and (max-width: 720px) {
    .shopping__cart__container {
        flex-direction: column-reverse;
    }

    .cart__course__total button {
        min-width: 100%;
    }
}

@media screen and (max-width: 530px) {
    .shopping__cart__list__item {
        flex-direction: column;
    }

    .cart__price__checkout {
        justify-content: space-between;
    }

    .shopping__cart__list__item img {
        width: 80px;
        height: 80px;
    }
}

@media screen and (max-width: 360px) {
    .shopping__cart__container, .cart__wrapper h2 {
        max-width: 85%;
    }
}



