.signup__form{
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 340px;
  margin: 50px auto;
  padding: 30px;
  background-color: #fdfffc;
}

.signup__form h3{
  margin-bottom: 40px;
  width: 100%;
  font-size: 1.375rem;
  font-weight: 500;
  text-align: center;
}

.signup__form label{
  width: 100%;
  margin-bottom: 10px;
  font-size: 1rem;
  font-weight: 500;
  text-align: start;
}

.signup__form input{
  width: 100%;
  margin-bottom: 30px;
  padding: 10px 0 4px;
  border: none;
  border-bottom: 1px solid #18ac9d;
  background-color: transparent;
  font-size: 1rem;
  font-weight: 400;
}

.signup__form input::-webkit-input-placeholder{
  color: #18ac9d;
  font-size: 0.875rem;
}

.signup__form input:-ms-input-placeholder{
  color: #18ac9d;
  font-size: 0.875rem;
}

.signup__form input::placeholder{
  color: #18ac9d;
  font-size: 0.875rem;
}

::-moz-placeholder{
  opacity: 1;
}

.signup__form input:hover,
.signup__form input:focus{
  border-bottom: 2px solid #18ac9d;
  margin-bottom: 29px;
  outline: none;
}

.signup__form button{
  width: 100%;
  margin-bottom: 30px;
  padding: 12px 16px;
  border: 1px solid #18ac9d;
  background-color: transparent;
  color: #18ac9d;
  font-size:  0.938rem;
  font-weight: 500;
  letter-spacing: 0.025rem;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 300ms;
}

.signup__form button:hover{
  background-color: #18ac9d;
  color: #edf2f4;
}

.signup__form p{
  font-size: 0.875rem;
  font-weight: 400;
}

.signup__form p.error{
  margin-bottom: 10px;
  color: #e71d36;
}

.signup__form a{
  color: #18ac9d;
  text-decoration: none;
}

.signup__form a:hover{
  text-decoration: underline;
}

@media (min-width: 375px){
  .signup__form h3{
    font-size: 1.5rem;
  }
}

@media (min-width: 600px){
  .signup__form h3{
    font-size: 1.75rem;
  }
}

@media (min-width: 1024px){
  .signup__form h3{
    font-size: 2rem;
  }
}
